.container {
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
header {
  display: -ms-flexbox;
  display: flex;
  height: 120px;
  background-color: var(--white);
}
main {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
}
footer {
  display: -ms-flexbox;
  display: flex;
  height: 5rem;
  background-color: var(--main-normal);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

/* template */
.header__logo {
  display: -ms-flexbox;
  display: flex;
  background-image: url("https://fakeimg.pl/120x120/");
  background-size: cover;
  background-position: 50% 50%;
  height: 120px;
  width: 120px;
}
.header__titles {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  background-color: rgb(139, 139, 226);
}



