.home {
  width: 100%;
}
.home__banner {
  height: 350px;
  width: 100%;
  background-image: url("https://fakeimg.pl/875x350/");
  background-size: cover;
  background-position: 50% 50%;
  display: -ms-flexbox;
  display: flex;
  margin: var(--div-margin);
}

.home__content {
  width: 100%;
  background-color: var(--main-light37);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-direction: column;
      flex-direction: column;
}
.home__welcome {
  width: 85%;
  margin: var(--div-margin);
}

.home__content__instruction {
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: var(--div-margin);
}

.home__content__btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  margin: var(--div-margin);
  text-decoration: none;
}



